body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

body h2, body p {
  margin: 0;
}

* {
  box-sizing: border-box;
}

.header {
  box-sizing: border-box;
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  height: 40px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  padding-left: 20px;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__link:hover {
  color: #00acdc;
  cursor: pointer;
}

.nav__item:first-child {
  padding-left: 0;
}

.catalog {
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
  position: relative;
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 488px) and (width <= 767px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width <= 487px) {
  .catalog {
    grid-template-columns: repeat(1, 200px);
  }
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-shrink: 0;
  width: 200px;
  height: 408px;
  padding: 0 16px;
  transition: all .3s ease-in-out;
}

.card__product-name {
  color: #060b35;
  width: 100%;
  margin: 40px 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__product-name {
  color: #34568b;
}

.card__product-img {
  width: 160px;
  height: 134px;
  margin: 32px auto 0;
  display: flex;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__product-price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 24px 0 16px;
  display: flex;
}

.card__product-price_text {
  color: #616070;
  font-size: 12px;
}

.card__product-price_value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__purchase-link {
  text-align: center;
  color: #fff;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__purchase-link:hover {
  box-sizing: border-box;
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.stars {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars__reviews {
  color: #060b35;
  margin-left: auto;
  font-size: 10px;
  font-weight: 400;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

